.main-header__inner {
  position: relative;
  height: 750px;
}

.main-header__inner--bg img {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  width: 100%;
  max-width: 100%;
  object-fit: cover;
  height: auto;
}

.main-header__inner--top {
  grid-template-columns: 1fr 1fr;
  padding: 15px 0;
}

.main-header__inner--top .item-title {
  color: #fff;
  font-size: 13px;
}

.main-header__inner--top .item-title span {
  color: #8b8c8b;
}

.main-header__inner--top .item-link {
  color: #8b8c8b;
  font-size: 13px;
  text-decoration: none;
}

.main-header__inner--top---left, .main-header__inner--top---right {
  display: flex;
}

.main-header__inner--top---left div {
  margin-right: 50px;
}

.main-header__inner--top---left div:nth-last-of-type() {
  margin-right: 0;
}

.main-header__inner--top---right {
  justify-content: flex-end;
}

.soc-icons {
  margin-right: 30px;
}

.soc-icons a {
  margin-right: 30px;
}
.soc-icons a:last-of-type {
  margin-right: 0;
}

.soc-icons .fa-brands {
  color: #999;
}

.flags a {
  margin-right: 15px;
}

.main-header__inner--nav {
  display: grid;
  grid-template-columns: 25% 65% 10%;
  align-items: center;
  position: relative;
}

.main-nav {
  position: relative;
}

.main-nav__list {
  list-style: none;
  display: flex;
  justify-content: flex-end;
  padding: 0 45px 0 0;
  margin: 0 0 16px 0;
}

.main-nav__item {
  margin-right: 25px;
}

.main-nav__item:last-of-type {
  margin-right: 0;
}

.main-nav__link {
  position: relative;
  color: #3a3535;
  text-decoration: none;
  text-transform: uppercase;
  font-size: 14px;
  padding: 10px 10px 16px 10px;
}
.main-nav__link .menu-icon-hover {
  display: none;
}
.main-nav__link::before {
  visibility: hidden;
  content: "";
  position: absolute;
  width: 100%;
  height: 3px;
  background: #ec1f27;
  bottom: 0;
  left: 0;
  transition: all 0.2s ease-in-out;
}
.main-nav__link:hover {
  color: #ec1f27;
}
.main-nav__link:hover .menu-icon-hover {
  display: block;
}
.main-nav__link:hover .menu-icon {
  display: none;
}
.main-nav__link:hover::before {
  visibility: visible;
}

.main-nav__link img {
  max-width: 70px;
  display: block;
  margin: 0 auto;
}

.main-nav svg {
  position: absolute;
  top: 15px;
  right: 0;
  width: 17px;
}

.search-item {
  cursor: pointer;
}

.toggle-menu {
  display: grid;
  justify-content: flex-end;
  align-self: flex-end;
  padding-bottom: 16px;
  cursor: pointer;
}

.toggle-menu .fa-solid {
  color: #ec1f27;
  font-size: 23px;
}

.toggle-menu .fa-xmark {
  display: none;
}

.hide-item .fa-xmark {
  display: block;
}

.hide-item .fa-bars {
  display: none;
}

#searchOverlay {
  height: 100%;
  width: 100%;
  display: none;
  position: fixed;
  z-index: 101;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.9);
}

#searchOverlay .closebtn {
  position: absolute;
  top: 20px;
  right: 45px;
  font-size: 60px;
  cursor: pointer;
  color: #fff;
}

#searchOverlay .overlay-content {
  position: relative;
  top: 46%;
