  text-align: center;
  margin-top: 30px;
  margin: auto;
}

#searchOverlay input[type=text] {
  padding: 15px;
  font-size: 17px;
  border: none;
  float: left;
  width: 80%;
  background: #fff;
  border-radius: 0;
}

#searchOverlay button {
  width: 20%;
  padding: 15px;
  background: #ddd;
  font-size: 17px;
  border: none;
  cursor: pointer;
  line-height: 20px;
  border-radius: 0;
}

#searchOverlay form {
  display: flex;
}

